import React from "react";
import Box from "@mui/material/Box";
import { Fonts } from "../../shared/constants/AppEnums";


const Account = () => {


  return (
    <>
      <Box
        component="h2"
        sx={{
          fontSize: 16,
          color: "text.primary",
          fontWeight: Fonts.SEMI_BOLD,
          mb: {
            xs: 2,
            lg: 4,
          },
        }}
      >
        My Account
      </Box>
   
    </>
  );
};

export default Account;
